    --background-color: #4a044e;
    --container-bx-shadow: 0px 0px 10px #fff;

    --header-background-color: #000;
    --header-text-color: #fffefe;
    
    --nav-add-stuff-btn-color: #7e22ce;
    --nav-text-color: #000;
    --nav-background-color: #f0eef1;

    --add-modal-header-bg-clr: #7e22ce;
    --add-modal-header-txt-clr: #fffefe;

    --add-modal-main-nav-bg-clr: #f0eef1;
    --add-modal-main-body-bg-clr: #fffefe;
    --add-modal-main-body-btn-border: #7e22ce;
    --add-modal-main-body-btn-txt-clr: #7e22ce;

    --priority-buttons-bg-clr: #fffefe;
    --low-priority-btn-border: green;

    --medium-priority-btn-border: orange;
    
    --high-priority-btn-border: red;

    --section-background-color: #fff;

    --task-card-clr: #f1f5f9;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    list-style: none;
    font-family: "Roboto", sans-serif;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100dvw;
    max-width: 100dvw;
    height: 100dvh;
    max-height: 100dvh;

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    background-repeat: no-repeat;
}
/*---------CONTAINER---------*//*---------CONTAINER---------*/
.container {
    height: 80dvh;
    max-height: 80dvh;
    width: 80dvw;
    max-width: 80dvw;

    background-color: var(--header-background-color);
    color: var(--header-text-color);

    border: 0;
    display: flex;
    flex-direction: column;
    box-shadow: var(--container-bx-shadow);
}
/*---------HEADER---------*//*---------HEADER---------*//*---------HEADER---------*/
header {
    padding: 1rem 2rem;
    font-size: 1.6rem;
}
header h1 {
    letter-spacing: 5px;
    color: var(--header-text-color);
}
/*---------MAIN---------*//*---------MAIN---------*//*---------MAIN---------*/
main {
    display: grid;
    grid-template-columns: 1fr 4fr;
    height: 100%;
}
main nav {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 1rem 0.8rem;
    max-width: 297px;

    position: relative;
    background-color: var(--nav-background-color);
    color: var(--nav-text-color);
}
main nav h2.nav-item.active::before {
    content: "// ";
}
main nav .project-div h2 {
    width: 100%;
    padding: 0.2rem 1rem;
    margin-top: 1rem;
    letter-spacing: 1px;
    font-weight: 700;
}
.project-container .project.active {
    color: var(--add-modal-header-bg-clr);
    font-weight: 700;
}
.project-container .project.active::before {
    content: "// ";
}
.project-container .project.active:hover {
    color: var(--add-modal-header-bg-clr);
    font-weight: 700;
}
main nav h2.nav-item:hover {
    color: var(--nav-add-stuff-btn-color);
    font-weight: 700;
}
main nav h2.nav-item:hover::before {
    content: "// ";
}
/*Add Stuff btn*/
main nav .add-project-btn {
    position: absolute;
    top: 87%;
    left: 10%;
}
main nav .add-project-btn span {
    background-color: var(--nav-add-stuff-btn-color);
    color: var(--header-text-color);
    font-size: 30px;
    padding: 0.7rem;
    border-radius: 50%;

    cursor: pointer;
    transition: all 0.3s ease;
}
main nav .add-project-btn span:hover {
    transform: scale(1.2);
    background: var(--nav-text-color);
    color: var(--header-text-color);
}
/*Projects*/
.projectElementWrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    word-wrap: break-word;
    
    width: 100%;
}
.projectElementWrapper span {
    color: red;
}
.project-div {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
}
.project-div h2 {
    cursor: auto;
}
.project-div .project-container {
    display: flex;
    flex-direction: column;
    max-height: 27rem;
    padding: 0 2rem;
    gap: 1.2rem;
    overflow-y: auto;
}
.project-div .project-container li {
    font-size: 1.3rem;
    margin-left: .5rem;

    cursor: pointer;
    word-wrap: break-word;

    transition: all 0s ease;
}
.project-div .project-container li:hover {
    color: var(--nav-add-stuff-btn-color);
    font-weight: 500;
}
.project-div .project-container li:hover::before {
    content: "// ";
}
.project-div p {
    margin-left: 10rem;
    color: #60a5fa;
    cursor: pointer;
    transition: all 0s ease;
}
.project-div p:hover {
    color: #1d4ed8;
}
/*---------DIALOG---------*//*---------DIALOG---------*//*----DIALOG----*/
dialog {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    outline: none;
    border: 0;
    box-shadow: 0px 0px 15px #fff;

    width: 55dvw;
    max-width: 55dvw;
}
dialog::backdrop {
    background: rgba(0, 0, 0, .8);
}
dialog form {
    display: flex;
    flex-direction: column;

    width: 100%;
}
/*ADD-MODAL-HEADER*/
.add-modal-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    padding: 1rem 2rem;
    font-size: 24px;
    background: var(--add-modal-header-bg-clr);
    color: var(--add-modal-header-txt-clr);
}
.add-modal-header span {
    font-size: 30px;
    cursor: pointer;
    transition: all .3s ease;
}
.add-modal-header span:hover {
    transform: scale(1.3);
}
/*ADD-MODAL-MAIN*/
.add-modal-main-body.descriptionModal {
    padding: 2em;
}
.tab-content {
    width: 100%;
    height: 50dvh;
    max-height: 50dvh;
    padding: 2rem;

    display: flex;
    flex-direction: column;

    position: relative;
}
.btn {
    position: absolute;
    top: 87%;
    left: 50%;
    transform: translateX(-50%);

    padding: .5rem 1rem;
    font-size: 1.1rem;

    cursor: pointer;
    color: var(--add-modal-main-body-btn-txt-clr);
    border: 1px solid var(--add-modal-main-body-btn-border);
    background: var(--add-modal-main-body-bg-clr);
    transition: all .3s ease;
    font-weight: 700;
}
.btn:hover {
    background: var(--add-modal-main-body-btn-txt-clr);
    color: var(--add-modal-main-nav-bg-clr);
}
input[type="text"],
#taskTitle,
#projectTitle {
    border: none;
    width: 100%;
    background: transparent;
    color: #000;
    font-size: 1.7rem;
    resize: none;

    transition: all .3s ease;
    word-wrap: break-word;
}
input[type="text"]:focus,
textarea:focus,
#taskTitle:focus,
#projectTitle:focus {
    outline: none;
}
textarea {
    width: 100%;
    height: 13rem;
    font-size: 1.3rem;
    margin-top: 2rem;
    background: transparent;

    border: none;
    resize: none;
}
textarea[disabled] {
    color: #000;
    font-size: 1.8rem;
}
.descriptionModal p:first-child {
    font-size: 2.2em;
    text-align: center;
}
.descriptionModal p:nth-child(2) {
    font-size: 1.8em;
    text-align: center;
    color: green;
    font-weight: 700;
    letter-spacing: 1px;
}
.dueDate-container {
    display: flex;
    gap: 30px;

    margin-top: 1rem;
    align-items: center;
}
.dueDate-container p {
    font-size: 1.3rem;
}
.dueDate {
    font-size: 1.3rem;
    font-weight: 500;
    border: 1px solid var(--add-modal-header-bg-clr);
    color: var(--add-modal-header-bg-clr);
    text-transform: uppercase;
    border-radius: .8rem;

    padding: .3rem .7rem;
}
.priority {
    display: flex;
    align-items: center;

    gap: 2.5rem;
    margin-top: 1rem;
}
.priority p {
    font-size: 1.3rem;
}
.priority-buttons {
    display: flex;
    gap: 1.2rem;
}
.priority-label {
    font-size: 1.3rem;
}
/*---------SECTION---------*//*---------SECTION---------*//*----SECTION----*/
section {
    background: var(--section-background-color);
    color: #000;
    display: flex;
    flex-direction: column;
    padding: 2.8rem;
    gap: 1rem;

    position: relative;
}
.section-heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.section-heading .delete-completed-task-btn {
    color: #60a5fa;
    cursor: pointer;

    transition: all 0s ease;
}
.section-heading .delete-completed-task-btn:hover {
    color: #1d4ed8;
}
.task-cards-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    height: 80%;

    padding: 0 1rem;
    overflow-y: auto;
}
.task-card {
    width: 100%;
    padding: 0.8rem;
    position: relative;

    background: var(--task-card-clr);
    border-radius: 0 1.5rem 1.5rem 0;
}
.task-card p {
    margin-left: 4rem;
    font-size: 24px;
}
.task-card p:first-child {
    font-weight: 700;
}
.task-card p:nth-child(2) {
    margin-top: .4rem;
    font-size: 20px;
}
.task-card-btn span {
    cursor: pointer;
    font-size: 30px;
    transition: all .3s ease;
}
.delete-btn,
.edit-btn,
.detail-btn {
    position: absolute;
}
.delete-btn {
    top: 0.8rem;
    right: 1rem;
}
.delete-btn span:hover {
    color: red;
    transform: scale(1.2);
}
.edit-btn {
    top: 0.8rem;
    right: 3.3rem;
}
.edit-btn span:hover {
    color: green;
    transform: scale(1.2);
}
.detail-btn {
    top: .8rem;
    right: 6rem;
}
.detail-btn span:hover {
    color: rgb(50, 50, 232);
}
input[type="checkbox"] {
    width: 20px;
    height: 20px;

    position: absolute;
    top: 50%;
    left: 25px;

    transform: translateY(-50%);
}

.high-priority-task {
    border-left: 7px solid var(--high-priority-btn-border);
}
.medium-priority-task {
    border-left: 7px solid var(--medium-priority-btn-border);
}
.low-priority-task {
    border-left: 7px solid var(--low-priority-btn-border);
