-----HEADER---------*/\nheader {\n    padding: 1rem 2rem;\n    font-size: 1.6rem;\n}\nheader h1 {\n    letter-spacing: 5px;\n    color: var(--header-text-color);\n}\n/*---------MAIN---------*//*---------MAIN---------*//*---------MAIN---------*/\nmain {\n    display: grid;\n    grid-template-columns: 1fr 4fr;\n    height: 100%;\n}\nmain nav {\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n    padding: 1rem 0.8rem;\n    max-width: 297px;\n\n    position: relative;\n    background-color: var(--nav-background-color);\n    color: var(--nav-text-color);\n}\nmain nav h2.nav-item.active::before {\n    content: \"// \";\n}\nmain nav .project-div h2 {\n    width: 100%;\n    padding: 0.2rem 1rem;\n    margin-top: 1rem;\n    letter-spacing: 1px;\n    font-weight: 700;\n}\n.project-container .project.active {\n    color: var(--add-modal-header-bg-clr);\n    font-weight: 700;\n}\n.project-container .project.active::before {\n    content: \"// \";\n}\n.project-container .project.active:hover {\n    color: var(--add-modal-header-bg-clr);\n    font-weight: 700;\n}\nmain nav h2.nav-item:hover {\n    color: var(--nav-add-stuff-btn-color);\n    font-weight: 700;\n}\nmain nav h2.nav-item:hover::before {\n    content: \"// \";\n}\n/*Add Stuff btn*/\nmain nav .add-project-btn {\n    position: absolute;\n    top: 87%;\n    left: 10%;\n}\nmain nav .add-project-btn span {\n    background-color: var(--nav-add-stuff-btn-color);\n    color: var(--header-text-color);\n    font-size: 30px;\n    padding: 0.7rem;\n    border-radius: 50%;\n\n    cursor: pointer;\n    transition: all 0.3s ease;\n}\nmain nav .add-project-btn span:hover {\n    transform: scale(1.2);\n    background: var(--nav-text-color);\n    color: var(--header-text-color);\n}\n/*Projects*/\n.projectElementWrapper {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    word-wrap: break-word;\n    \n    width: 100%;\n}\n.projectElementWrapper span {\n    color: red;\n}\n.project-div {\n    display: flex;\n    flex-direction: column;\n    gap: 0.8rem;\n}\n.project-div h2 {\n    cursor: auto;\n}\n.project-div .project-container {\n    display: flex;\n    flex-direction: column;\n    max-height: 27rem;\n    padding: 0 2rem;\n    gap: 1.2rem;\n    overflow-y: auto;\n}\n.project-div .project-container li {\n    font-size: 1.3rem;\n    margin-left: .5rem;\n\n    cursor: pointer;\n    word-wrap: break-word;\n\n    transition: all 0s ease;\n}\n.project-div .project-container li:hover {\n    color: var(--nav-add-stuff-btn-color);\n    font-weight: 500;\n}\n.project-div .project-container li:hover::before {\n    content: \"// \";\n}\n.project-div p {\n    margin-left: 10rem;\n    color: #60a5fa;\n    cursor: pointer;\n    transition: all 0s ease;\n}\n.project-div p:hover {\n    color: #1d4ed8;\n}\n/*---------DIALOG---------*//*---------DIALOG---------*//*----DIALOG----*/\ndialog {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    outline: none;\n    border: 0;\n    box-shadow: 0px 0px 15px #fff;\n\n    width: 55dvw;\n    max-width: 55dvw;\n}\ndialog::backdrop {\n    background: rgba(0, 0, 0, .8);\n}\ndialog form {\n    display: flex;\n    flex-direction: column;\n\n    width: 100%;\n}\n/*ADD-MODAL-HEADER*/\n.add-modal-header {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: space-between;\n\n    padding: 1rem 2rem;\n    font-size: 24px;\n    background: var(--add-modal-header-bg-clr);\n    color: var(--add-modal-header-txt-clr);\n}\n.add-modal-header span {\n    font-size: 30px;\n    cursor: pointer;\n    transition: all .3s ease;\n}\n.add-modal-header span:hover {\n    transform: scale(1.3);\n}\n/*ADD-MODAL-MAIN*/\n.add-modal-main-body.descriptionModal {\n    padding: 2em;\n}\n.tab-content {\n    width: 100%;\n    height: 50dvh;\n    max-height: 50dvh;\n    padding: 2rem;\n\n    display: flex;\n    flex-direction: column;\n\n    position: relative;\n}\n.btn {\n    position: absolute;\n    top: 87%;\n    left: 50%;\n    transform: translateX(-50%);\n\n    padding: .5rem 1rem;\n    font-size: 1.1rem;\n\n    cursor: pointer;\n    color: var(--add-modal-main-body-btn-txt-clr);\n    border: 1px solid var(--add-modal-main-body-btn-border);\n    background: var(--add-modal-main-body-bg-clr);\n    transition: all .3s ease;\n    font-weight: 700;\n}\n.btn:hover {\n    background: var(--add-modal-main-body-btn-txt-clr);\n    color: var(--add-modal-main-nav-bg-clr);\n}\ninput[type=\"text\"],\n#taskTitle,\n#projectTitle {\n    border: none;\n    width: 100%;\n    background: transparent;\n    color: #000;\n    font-size: 1.7rem;\n    resize: none;\n\n    transition: all .3s ease;\n    word-wrap: break-word;\n}\ninput[type=\"text\"]:focus,\ntextarea:focus,\n#taskTitle:focus,\n#projectTitle:focus {\n    outline: none;\n}\ntextarea {\n    width: 100%;\n    height: 13rem;\n    font-size: 1.3rem;\n    margin-top: 2rem;\n    background: transparent;\n\n    border: none;\n    resize: none;\n}\ntextarea[disabled] {\n    color: #000;\n    font-size: 1.8rem;\n}\n.descriptionModal p:first-child {\n    font-size: 2.2em;\n    text-align: center;\n}\n.descriptionModal p:nth-child(2) {\n    font-size: 1.8em;\n    text-align: center;\n    color: green;\n    font-weight: 700;\n    letter-spacing: 1px;\n}\n.dueDate-container {\n    display: flex;\n    gap: 30px;\n\n    margin-top: 1rem;\n    align-items: center;\n}\n.dueDate-container p {\n    font-size: 1.3rem;\n}\n.dueDate {\n    font-size: 1.3rem;\n    font-weight: 500;\n    border: 1px solid var(--add-modal-header-bg-clr);\n    color: var(--add-modal-header-bg-clr);\n    text-transform: uppercase;\n    border-radius: .8rem;\n\n    padding: .3rem .7rem;\n}\n.priority {\n    display: flex;\n    align-items: center;\n\n    gap: 2.5rem;\n    margin-top: 1rem;\n}\n.priority p {\n    font-size: 1.3rem;\n}\n.priority-buttons {\n    display: flex;\n    gap: 1.2rem;\n}\n.priority-label {\n    font-size: 1.3rem;\n}\n/*---------SECTION---------*//*---------SECTION---------*//*----SECTION----*/\nsection {\n    background: var(--section-background-color);\n    color: #000;\n    display: flex;\n    flex-direction: column;\n    padding: 2.8rem;\n    gap: 1rem;\n\n    position: relative;\n}\n.section-heading {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n}\n.section-heading .delete-completed-task-btn {\n    color: #60a5fa;\n    cursor: pointer;\n\n    transition: all 0s ease;\n}\n.section-heading .delete-completed-task-btn:hover {\n    color: #1d4ed8;\n}\n.task-cards-container {\n    display: flex;\n    flex-direction: column;\n    gap: 20px;\n    width: 100%;\n    height: 80%;\n\n    padding: 0 1rem;\n    overflow-y: auto;\n}\n.task-card {\n    width: 100%;\n    padding: 0.8rem;\n    position: relative;\n\n    background: var(--task-card-clr);\n    border-radius: 0 1.5rem 1.5rem 0;\n}\n.task-card p {\n    margin-left: 4rem;\n    font-size: 24px;\n}\n.task-card p:first-child {\n    font-weight: 700;\n}\n.task-card p:nth-child(2) {\n    margin-top: .4rem;\n    font-size: 20px;\n}\n.task-card-btn span {\n    cursor: pointer;\n    font-size: 30px;\n    transition: all .3s ease;\n}\n.delete-btn,\n.edit-btn,\n.detail-btn {\n    position: absolute;\n}\n.delete-btn {\n    top: 0.8rem;\n    right: 1rem;\n}\n.delete-btn span:hover {\n    color: red;\n    transform: scale(1.2);\n}\n.edit-btn {\n    top: 0.8rem;\n    right: 3.3rem;\n}\n.edit-btn span:hover {\n    color: green;\n    transform: scale(1.2);\n}\n.detail-btn {\n    top: .8rem;\n    right: 6rem;\n}\n.detail-btn span:hover {\n    color: rgb(50, 50, 232);\n}\ninput[type=\"checkbox\"] {\n    width: 20px;\n    height: 20px;\n\n    position: absolute;\n    top: 50%;\n    left: 25px;\n\n    transform: translateY(-50%);\n}\n\n.high-priority-task {\n    border-left: 7px solid var(--high-priority-btn-border);\n}\n.medium-priority-task {\n    border-left: 7px solid var(--medium-priority-btn-border);\n}\n.low-priority-task {\n    border-left: 7px solid var(--low-priority-btn-border);\n}"],"sourceRoot":""}]);
