export const renderTasks = (() => {
    const Template = document.querySelector('#task-template');

    function createTaskCard(task, index) {
        const taskTemplate = document.importNode(Template.content, true);
        const taskCard = taskTemplate.querySelector('.task-card');

        const taskTitleDisplay = taskCard.querySelector('#task-name-display');
        const taskDuedateDisplay = taskCard.querySelector('#dueDate-display');
        const checkbox = taskCard.querySelector('input');

        taskTitleDisplay.textContent = task.title;
        taskDuedateDisplay.textContent = task.dueDate;
        checkbox.checked = task.completed;
        checkbox.id = task.id;
        taskCard.dataset.taskId = index;
        taskCard.classList.add(`${task.priority}-priority-task`);

        return taskCard;
    }

    function renderTask(taskList) {
        const taskContainer = document.querySelector('[data-tasks-container]');

        clearHTMLTaskList();
        taskList.forEach((task, index) => {
            const taskCard = createTaskCard(task, index);
            taskContainer.append(taskCard);
        });
    }

    function clearHTMLTaskList() {
        const taskContainer = document.querySelector('[data-tasks-container]');

        while (taskContainer.firstChild) {
            taskContainer.removeChild(taskContainer.firstChild);
        }
    }

    return {
        renderTask,
    }
})();