export const renderProjects = (() => {
    const projectContainer = document.querySelector('.project-container');

    function createProjectElement(arg) {
        const projectElement = document.createElement('li');
        projectElement.textContent = arg.name;
        projectElement.classList.add('project');
        projectElement.dataset.projectId = arg.id;

        return projectElement;
    }
    
    function renderProject(arg) {
        clearHTMLProjectList();
        arg.forEach((project) => {
            const projectElement = createProjectElement(project);
            projectContainer.append(projectElement);
        });
    }

    function clearHTMLProjectList() {
        while (projectContainer.firstChild) {
            projectContainer.removeChild(projectContainer.firstChild);
        }
    }

    return { 
        renderProject, 
    };

})();

