import { Project } from "./projectClass";
import { Task } from "./taskClass";
import { renderProjects } from "./projects";
import { renderTasks } from "./tasks";
import { setItem, getItem } from "./localStorage";
import { format } from "date-fns";

export const central = (() => {
    const projectList = getItem('projectList') || [];
    let activeProjectIdentifier = null;

    const saveProjects = () => setItem('projectList', projectList);

    const updateProjectDisplay = () => renderProjects.renderProject(projectList);

    const updateTaskDisplay = () => {
        const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
        if (selectedProject) renderTasks.renderTask(selectedProject.tasks);
    };

    const createProject = (name) => {
        const project = new Project(name);
        projectList.push(project);
        saveProjects();
    };

    const deleteProject = () => {
        const dltProjectBtn = document.querySelector('.deleteProject-btn');
        
        dltProjectBtn.addEventListener('click', () => {
            const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
            
            if (selectedProject) {
                projectList.splice(projectList.indexOf(selectedProject), 1);
                activeProjectIdentifier = null;
                saveProjects();
                updateProjectDisplay();
                updateTaskDisplay();
            }
        });
    };

    const displayHeading = () => {
        const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
        const sectionHeadingDisplay = document.querySelector('.page-heading');
        sectionHeadingDisplay.textContent = selectedProject ? selectedProject.name : 'Select a Project to View Tasks';
    };

    const taskModalListener = () => {
        const taskForm = document.querySelector('[data-add-task-form]');
        const taskModal = document.querySelector('[data-add-task-dialog]');
        const addTaskBtn = document.querySelector('[data-create-new-task-btn]');
        const closeTaskModalBtn = document.querySelector('[data-add-task-modal-close-btn]');

        addTaskBtn.addEventListener('click', () => taskModal.showModal());
        closeTaskModalBtn.addEventListener('click', () => {
            taskModal.close();
            taskForm.reset();
        });

        taskForm.addEventListener('submit', (e) => {
            e.preventDefault();
            createTask();
            taskForm.reset();
            updateTaskDisplay();
            taskModal.close();
        });
    };

    const createTask = () => {
        const taskForm = document.querySelector('[data-add-task-form]');
        const taskTitleInput = taskForm.querySelector('[data-taskTitle]');
        const taskDuedateInput = taskForm.querySelector('#dueDate');
        const formattedDate = format(taskDuedateInput.value, 'd/MMM/yyyy');
        const taskDetailInput = taskForm.querySelector('[data-taskDetails]');
        const selectedPriority = taskForm.querySelector('input[name="priority-btn"]:checked');
        const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);

        if (!selectedProject) {
            alert('Please Select a Project before adding a Task!');
            return;
        }

        const newTask = new Task(taskTitleInput.value.trim(), taskDetailInput.value, formattedDate, selectedPriority.value);
        selectedProject.tasks.push(newTask);
        saveProjects();
    };

    const checkCompleteCheckbox = () => {
        const taskContainer = document.querySelector('[data-tasks-container]');
        taskContainer.addEventListener('change', (e) => {
            if (e.target.tagName.toLowerCase() === 'input') {
                const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
                const selectedTask = selectedProject?.tasks.find(task => task.id === e.target.id);
                if (selectedTask) selectedTask.completed = e.target.checked;
                saveProjects();
            }
        });
    };

    const dltCompletedTask = () => {
        const dltCompletedTaskBtn = document.querySelector('[data-dlt-completed-tasks]');
        dltCompletedTaskBtn.addEventListener('click', () => {
            const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
            if (selectedProject) {
                selectedProject.tasks = selectedProject.tasks.filter(task => !task.completed);
                saveProjects();
                updateTaskDisplay();
            }
        });
    };

    const taskContainerEventListener = () => {
        const taskContainer = document.querySelector('[data-tasks-container]');
        const descriptionModal = document.querySelector('[data-detail-modal]');
        const editModal = document.querySelector('[data-edit-task-dialog]');

        taskContainer.addEventListener('click', (e) => {
            const clickedElement = e.target;
            const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
            const taskCard = clickedElement.closest('.task-card');
            if (!taskCard) return;

            const taskIndex = taskCard.dataset.taskId;
            const selectedTask = selectedProject?.tasks[taskIndex];

            if (!selectedTask) return;

            if (clickedElement.textContent.trim().toLowerCase() === 'feed') {
                descriptionModal.showModal();
                displayTaskDescription(selectedTask);
            }

            if (clickedElement.textContent.trim().toLowerCase() === 'edit_note') {
                editModal.showModal();
                showEditTaskForm(selectedTask);
                handleEditTask(selectedTask);
            }

            if (clickedElement.textContent.trim().toLowerCase() === 'delete_outline') {
                deleteTask(taskIndex);
                updateTaskDisplay();
            }
        });

        closeModal(descriptionModal, '[data-close-task-description]');
        closeModal(editModal, '[data-edit-task-modal-close-btn]');
    };

    const closeModal = (modal, selector) => {
        const closeBtn = document.querySelector(selector);
        closeBtn.addEventListener('click', () => modal.close());
    };

    const displayTaskDescription = (task) => {
        const descriptionModal = document.querySelector('[data-detail-modal]');
        descriptionModal.querySelector('[data-task-name-description]').textContent = task.title;
        descriptionModal.querySelector('[data-task-dueDate-description]').textContent = `Due Date: ${task.dueDate}`;
        descriptionModal.querySelector('#task-description').textContent = task.detail;
    };

    const showEditTaskForm = (task) => {
        const editForm = document.querySelector('[data-edit-task-form]');
        editForm.querySelector('[data-editTaskTitle]').value = task.title;
        editForm.querySelector('[data-EditTaskDetails]').value = task.detail;
    };

    const handleEditTask = (task) => {
        const editForm = document.querySelector('[data-edit-task-form]');
        const editModal = document.querySelector('[data-edit-task-dialog]');

        editForm.addEventListener('submit', (e) => {
            e.preventDefault();

            const taskTitleInput = editForm.querySelector('[data-editTaskTitle]');
            const taskDetailInput = editForm.querySelector('[data-EditTaskDetails]');
            const taskDuedateInput = editForm.querySelector('#editDuedate');
            const formattedDate = format(taskDuedateInput.value, 'd/MMM/yyyy');
            const selectedPriority = editForm.querySelector('input[name="edit-priority-btn"]:checked');

            task.title = taskTitleInput.value.trim();
            task.detail = taskDetailInput.value.trim();
            task.dueDate = formattedDate;
            task.priority = selectedPriority.value;

            updateTaskDisplay();
            saveProjects();
            editModal.close();
        });
    };

    const deleteTask = (index) => {
        const selectedProject = projectList.find(project => project.id === activeProjectIdentifier);
        if (selectedProject) selectedProject.tasks.splice(index, 1);
        saveProjects();
    };

    const projectModalListener = () => {
        const projectForm = document.querySelector('[data-add-project-form]');
        const projectNameInput = document.querySelector('[data-projectTitle]');
        const addProjectBtn = document.querySelector('[data-add-project-btn]');
        const addProjectModal = document.querySelector('[data-add-project-dialog]');
        const closeProjectModalBtn = document.querySelector('[data-add-project-modal-close-btn]');

        addProjectBtn.addEventListener('click', () => addProjectModal.showModal());
        closeProjectModalBtn.addEventListener('click', () => addProjectModal.close());

        projectForm.addEventListener('submit', (e) => {
            e.preventDefault();
            createProject(projectNameInput.value.trim());
            projectForm.reset();
            addProjectModal.close();
            updateProjectDisplay();
        });
    };

    const projectIdentify = () => {
        const projectContainer = document.querySelector('.project-container');
        projectContainer.addEventListener('click', (e) => {
            if (e.target.tagName.toLowerCase() === 'li') {
                activeProjectIdentifier = e.target.dataset.projectId;

                const previousSelectedProject = document.querySelector('.active');
                if (previousSelectedProject) previousSelectedProject.classList.remove('active');
                e.target.classList.toggle('active', e.target.dataset.projectId === activeProjectIdentifier);

                displayHeading();
                updateTaskDisplay();
            }
        });
    };

    const initialize = () => {
        updateProjectDisplay();
        setupEventListeners();
        displayHeading();
    };

    const setupEventListeners = () => {
        projectModalListener();
        projectIdentify();
        deleteProject();
        taskModalListener();
        checkCompleteCheckbox();
        dltCompletedTask();
        taskContainerEventListener();
    };

    return { initialize };
})();
